<template>
<el-container>
  <el-header>感谢浏览本网页</el-header>
<el-main>
    <div>
        <p>网站是我在闲暇之余制作的，由于制作水平有限，难免会有很多的问题，希望大家多多包涵。</p>
        <p>由于流量人数增加，之前服务器和CDN的免费流量已经用完了，如果本网站提升了您的游戏体验</p>
        <p>还需要您的支持和帮助。</p>
        <img src="../assets/image/wx.png">
        <img src="../assets/image/zfb.jpg">
    <div style="width:70%;margin:0px auto; padding-top:2rem">
    <el-form label-position='top'  ref="form" :model="form" label-width="10rem">
        <el-form-item label="您的留言" >
            <el-input type='textarea' v-model="form.message"></el-input>
        </el-form-item>
        <el-form-item>
            <el-button type="primary" @click="onSubmit()">提交</el-button>
            <el-button @click="intoLogin()">进入登录页面</el-button>
        </el-form-item>
</el-form>
</div>
    </div>
  </el-main>
</el-container>
  
</template>

<script>
import AV from 'leancloud-storage'
export default {
    name: 'info',
    data() {
        return {
            form: {
                message: '',
            }
        }
    },
    methods: {
        onSubmit() {
            var that = this
            if(this.form.message == '' ||this.form.message ==null){
                this.$layer.alert("请输入内容！");
                return
            }
            var TodoFolder = AV.Object.extend('userMessage');
            var todoFolder = new TodoFolder();
            todoFolder.set('message', this.form.message);
            todoFolder.save().then(function (todo) {
                that.$message('提交成功，请进入登录页面')
                console.log('objectId is ' + todo.id);
            }, function (error) {
                console.error(error);
            });
        },
        intoLogin() {
            this.$router.push({
                path: 'login'
            })
        }
    }
}
</script>

<style>
.el-header,
.el-footer {
  /* background-color: #b3c0d1; */
  /* color: #333; */
  text-align: center;
  line-height: 60px;
}

.el-aside {
  background-color: #d3dce6;
  color: #333;
  text-align: center;
  line-height: 200px;
}

.el-main {
  /* background-color: #e9eef3; */
  /* color: #333; */
  text-align: center;
  line-height: 20px;
}

body > .el-container {
  margin-bottom: 40px;
}

.el-container:nth-child(5) .el-aside,
.el-container:nth-child(6) .el-aside {
  line-height: 260px;
}

.el-container:nth-child(7) .el-aside {
  line-height: 320px;
}
</style>




